    export default {
        data() {
            return {
				sessionId:'tourist'
            }
        },
        onLoad() {
            let self = this
            wx.login({
                success(res) {
                    if (res.code) {
                        console.log('wechat_code:'+res.code)
                        uni.request({
                            url: 'http://172.27.1.207:8009/rmserver/login-in',
                            method: 'POST',
                            data: {
                                "code": res.code
                            },
                            header: {
                                "Gowild-SessionId": "tourist"
                            },
                            success: sessionRes => {
								console.log('login resp:')
                                console.log(sessionRes.data)
								try {
								    uni.setStorageSync('sessionId', sessionRes.data.data[0].sessionId);
								} catch (e) {
									console.log('set session id err')
								    console.log(e)
								}
                            },
                            fail: () => {

                            },
                            complete: () => {

                            }
                        })
                    } else {
                        console.log('登录失败！' + res.errMsg)
                    }
                }
            })
        },
        methods: {
            openTape() {
                uni.navigateTo({
                    url: '../tape/tape'
                })
            },
            openHistory() {
                uni.navigateTo({
                    url: '../history/history'
                })
            },
            openHistory2() {
                uni.navigateTo({
                    url: '../history/history2'
                })
            }
        }
    }
